import React from "react";
import { Table } from "react-bootstrap";
import { connect } from "react-redux";

function Cart(props) {
  return (
    <div>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경</th>
          </tr>
        </thead>
        <tbody>
          {props.state.map((shoe, i) => {
            return (
              <tr key={i}>
                <td>{i + 1}</td>
                <td>{shoe.name}</td>
                <td>{shoe.quan}</td>
                <td>
                  <button
                    onClick={() => {
                      props.dispatch({ type: "증가", id: shoe.id });
                    }}
                  >
                    +
                  </button>
                  <button
                    onClick={() => {
                      props.dispatch({ type: "감소", id: shoe.id });
                    }}
                  >
                    -
                  </button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </Table>
      {props.alert === true ? (
        <div className="my-alert2">
          <p>지금 구매하시면 20% 할인</p>
          <button
            onClick={() => {
              props.dispatch({ type: "닫기" });
            }}
          >
            닫기
          </button>
        </div>
      ) : null}
    </div>
  );
}

function state를props화(state) {
  return {
    state: state.reducer,
    alert: state.reducer2,
  };
}
export default connect(state를props화)(Cart);
